import React from 'react';
import * as actions from '../../store/actions/auth';
import { connect } from 'react-redux'
import { Steps, Icon } from 'antd';

const { Step } = Steps;
class VerificationSent extends React.Component {
    render() {
        return (
            <div>
            {
                this.props.success === false ?
                this.props.history.push('/')
                :

            
            <Steps style={{width: '70%', marginLeft: '10%'}}>
                <Step status="finish" title="" description="
                      ." icon={<Icon type="user" />} />
                <Step status="progress" title=" " description="
                    admin        " icon={<Icon type="loading" />} />
                <Step status="wait" title="!" description="
                 ,        ." icon={<Icon type="smile" />} />
            </Steps>
            }
        </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        token: state.authReducer.token,
        success: state.authReducer.success
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onAuth: (username, password) => dispatch(actions.authLogin(username, password)) 
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(VerificationSent);