import { Form, Icon, Input, Button, Modal, Radio, Alert } from 'antd';
import React from 'react';
import axios from 'axios';


const backendUrl = 'http://0.0.0.0:8000/crowler/notify/categories/update/'

const token = localStorage.getItem('token');


if (token != null) {
    axios.defaults.headers = {
      "Content-Type": "application/json",
      Authorization: 'Token ' + token}
    }

class CategoriesUpdate extends React.Component {
    constructor(props){
        super(props);
        this.handleVerification = this.handleVerification.bind(this);
     }

    state = {
        categoriesRadio: 'add',
        success: '',
        error: ''
    }

    handleVerification = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            axios
                .post(backendUrl, {
                    category: values.username,
                    action: this.state.categoriesRadio
                    })
                .then(res => {
                    this.setState({
                        success: res.data,
                        error: '',
                    })
                })
            
                .catch(error => {
                    this.setState({
                        error: error.response.data,
                        success: '',
                    })
                
            })
            
          }
        });
      };
    
      verificationRadioChanger = e => {
        this.setState({
            categoriesRadio: e.target.value,
        });
      };
    
      verificationInfo() {
        Modal.info({
          title: ' ',
          content: (<div>
            <p>     ,       .</p>
            <p>    ,    URL(      )  ,     ""</p>
            <p>: "kanctovary"  "kanctovary/shkolnye-tovary"  "kanctovary/shkolnye-tovary/globusy" ( )</p>
            <p>     ,    admin.</p>
          </div>),
          onOk() {},
        });
      }

    render() {
        let errorMessage = null;
        let successMessage = null;

        
        if (this.state.error) {
            errorMessage =(
                <Alert style={{marginBottom: '25px'}} message={this.state.error} type="error" />
            );
        }
        if (this.state.success) {
            successMessage =(
                <Alert style={{marginBottom: '25px'}} message={this.state.success} type="success" />
            );
        }

    
        const { getFieldDecorator } = this.props.form;
        return(
            <div>
                {errorMessage}
                {successMessage}
            <h1>   </h1>
                <Button icon="question-circle" style={{'position': 'absolute', 'right': '10%', 'top': '25%', marginBottom: '20px'}} onClick={this.verificationInfo}></Button>

                <Form onSubmit={this.handleVerification} className="login-form">
                    <Form.Item style={{marginTop: '20px'}}>
                    {getFieldDecorator('username', {
                        rules: [{ required: true, message: '    ,     ' }],
                    })(
                        <Input
                        prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                        placeholder="   "
                        />,
                    )}
                    </Form.Item>

                <Radio.Group onChange={this.verificationRadioChanger} value={this.state.categoriesRadio}>
                    <Radio value={'add'}></Radio>
                    <Radio value={'remove'}></Radio>
                </Radio.Group>

                <Form.Item>
                    <Button style={{marginTop: '20px'}}type="primary" htmlType="submit" className="login-form-button">
                        
                    </Button>
                </Form.Item>
        </Form>
      </div>
        )
    }
}
const CategoriesUpdateForm = Form.create({ name: 'normal_login' })(CategoriesUpdate);

export default CategoriesUpdateForm